      <div class="d-flex mt-2">
        <p>å·²å®Œæˆ:</p>
      <div v-if='data.item.state === true'>âœ”</div>
      </div>
    </template>
  </b-table></div>
  <b-modal id="modal-state"
    title="æ“ä½œ"
    centered
    ok-variant='success'
    ok-title='é€å‡º'
    cancel-variant='danger'
    cancel-title='å–æ¶ˆ'
    @ok="submitModal"
    >
    <b-form-group label="ä¸Šæ¶">
      <b-form-radio v-model='form.state' :value='true'>å·²å®Œæˆ</b-form-radio>
      <b-form-radio v-model='form.state' :value='false'>æœªå®Œæˆ</b-form-radio>
    </b-form-group>
  </b-modal>
</b-container>
</template>

<script>
export default {
  data () {
    return {
      fields: [
